export default {
	
	data() {
		return {
			currentNumber: 0, // 用来判断active样式类是否显示
			statusChange: '',
			curPage: 1,
			status: 0,
			nameList: [],
			id: '',
			acid:'',
			list: [{
					title: "USDT"
				},
				{
					title: "HUSD"
				},
				{
					title: "BTC"
				},
				{
					title: "ETH"
				},
				{
					title: "HT"
				},
				{
					title: "ALTS"
				}
			]
		};
	},
	onLoad(options) {
		// if(!uni.getStorageSync("token")&&!uni.getStorageSync("SecretKey")){
		// 	this.$base1._isLogin()
		// }
		this.getCoreDetail()
	},
	onPullDownRefresh() {
		this.getCoreDetail()
		  setTimeout(function () {
			  uni.stopPullDownRefresh();
		  }, 1000);
	  },
	methods: {
		currentInfo(index) {
			this.currentNumber = index;
			console.log(this.currentNumber)
			this.status = this.currentNumber
			//行情动态 显示
			uni.request({
				url: this.baseUrl + "/investment-list?",
				data:{
					status:this.status
				},
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token'),
					SecretKey: uni.getStorageSync('SecretKey')
				},
				success: (res) => {
					// console.log(res)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if (res.data.status == 1) {
						this.nameList = res.data.data.data
						console.log(this.nameList)
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		//我的理财列表 显示全部
		getCoreDetail() {
			uni.request({
				url: this.baseUrl + "/investment-list?status=" + this.status,
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token'),
					SecretKey: uni.getStorageSync('SecretKey')
				},
				success: (res) => {
					
					// console.log(res.data)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if (res.data.status == 1) {
						this.nameList = res.data.data.data
						
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		showState(state){
			if(state==1){
				return  "待理财";
			}else if(state==2){
				return "理财中"
			}else if(state==3){
				return "已撤资"
			}else if(state==4){
				return "已完结"
			}
		},
		jumpToManage(index) {
			this.id = this.nameList[index].Id
			this.acid = this.nameList[index].ActivityId
			console.log(this.id)//我的理财id
			console.log(this.acid)//理财活动id
			uni.navigateTo({
				url: "./manage-in?id=" + this.id +"&acid="+this.acid//需要传一个investmentId过去,还需传一个acid过去获取天数
			})
		}
	}
}
